.preloader{flex:0 0 auto;width:250px;height:300px;position:relative}@media only screen and (max-width: 991px){.preloader{display:none}}@keyframes mainAnimation{0%{width:50px;transform:translateX(0px) translateY(0px) rotate(0deg)}20%{width:50px;transform:translateX(0px) translateY(0px) rotate(0deg)}40%{width:150px;transform:translateX(-50px) translateY(0px) rotate(0deg)}60%{width:150px;transform-origin:bottom right;transform:translateX(-150px) translateY(0px) rotate(90deg)}80%{width:50px;transform:translateX(-25px) translateY(0px) rotate(90deg)}100%{width:50px;transform:translateX(-50px) translateY(0px) rotate(90deg)}}@keyframes secundaryAnimation{0%{transform:translateX(0px) translateY(0px) rotate(0deg)}20%{transform:translateX(0px) translateY(0px) rotate(180deg)}40%{transform:translateX(0px) translateY(0px) rotate(180deg)}60%{transform:translateX(0px) translateY(0px) rotate(0deg)}80%{transform:translateX(0px) translateY(0px) rotate(0deg)}100%{transform:translateX(0px) translateY(0px) rotate(180deg)}}@keyframes shadowAnimation{0%{width:150px;transform:translateX(-25px) translateY(0px) rotate(0deg)}20%{width:75px;transform:translateX(37.5px) translateY(0px) rotate(0deg)}40%{width:200px;transform:translateX(-25px) translateY(0px) rotate(0deg)}60%{width:75px;transform:translateX(37.5px) translateY(0px) rotate(0deg)}80%{width:75px;transform:translateX(37.5px) translateY(0px) rotate(0deg)}100%{width:150px;transform:translateX(-25px) translateY(0px) rotate(0deg)}}@keyframes float{0%{top:80%}50%{top:80%}100%{top:80%}}.loader{position:absolute;width:50px;height:50px;top:50%;left:30%;background-color:#FFF5A5;transform:translateX(0px) translateY(0px) rotate(0deg);animation:mainAnimation 4s ease 0s infinite forwards;z-index:2}.loader:after{content:'';display:inline-block;position:absolute;width:50px;height:50px;top:0;left:-50px;background-color:#FFF5A5;transform-origin:top right;transform:translateX(0px) translateY(0px) rotate(0deg);animation:secundaryAnimation 4s ease 0s infinite forwards}.shadow{position:absolute;width:100px;height:10px;left:10%;margin-left:-75px;border-radius:50%;background-color:#3f4c6b;transform:translateX(0px) translateY(0px) rotate(0deg);animation:shadowAnimation 4s ease 0s infinite forwards, float 8s ease-in-out 0s infinite forwards;z-index:1}
